.certificate_content {
  position: relative;
}
.certificate_content .star_content {
  position: relative;
  height: 200px;
  text-align: center;
  justify-content: center;
  display: flex;
  width: 77%;
  margin: auto;
}
.certificate_content .star_content .star_rate_1 {
  position: absolute;
  left: 9px;
  top: 19%;
}
.certificate_content .star_content .star_rate_2 {
  position: absolute;
  left: 19%;
  top: 7px;
}
.certificate_content .star_content .star_rate_3 {
  position: absolute;
  left: 38%;
  top: -10px;
}
.certificate_content .star_content .star_rate_4 {
  position: absolute;
  right: 18%;
  top: 14px;
}
.certificate_content .star_content .star_rate_5 {
  position: absolute;
  right: 0px;
  top: 35px;
}
.certificate_content h1 {
  font-weight: 600;
  font-size: 22px;
  line-height: 29px;
  text-transform: uppercase;
  color: #4f4f4f;
  font-family: SegoeUI_medium;
  margin-bottom: 7px;
  margin-top: -40px;
}
.certificate_content p {
  font-family: SegoeUI_bold;
  font-weight: 900;
  font-size: 64px;
  line-height: 86px;
  text-transform: uppercase;
  color: #ec498a;
  margin-top: -15px;
}
.border_certificate {
  border-bottom: 1px solid #e0e0e0;
  width: 400px;
  margin: auto;
}
.clock_section_first,
.clock_section_second,
.clock_section_third {
  position: relative;
  display: inline-block;
  margin: 0px 20px;
  vertical-align: top;
}
.clock_section p {
  font-family: SegoeUI_bold;
  font-weight: bold;
  font-size: 14px;
  line-height: 19px;
  text-transform: capitalize;
  color: #dc2970;
  margin-bottom: 0px;
}
.clock_section span {
  font-family: SegoeUI_bold;
  font-weight: bold;
  font-size: 36px;
  line-height: 36px;
  text-transform: uppercase;
  color: #4b4b4b;
  display: block;
  width: 100%;
}
.clock_section_first_content {
  position: absolute;
  left: 0px;
  right: 0px;
  top: 45%;
  text-align: center;
}
.clock_section_second_content {
  position: absolute;
  left: 0px;
  right: 0px;
  top: 45%;
  text-align: center;
}
.clock_section_third_content {
  position: absolute;
  left: 0px;
  right: 0px;
  top: 45%;
  text-align: center;
}
.download_btn {
  background: #ec498a;
  border-radius: 10px;
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  font-family: SegoeUI_medium;
  text-align: center;
  color: #ffffff;
  display: inline-block;
  padding: 9px 13px;
  border: none;
}
.certificate_para p {
  font-family: SegoeUI_bold;
  font-weight: bold;
  font-size: 14px;
  line-height: 19px;
  text-align: center;
  color: #dc2970;
  margin-bottom: 0px;
}
.certificate_para span {
  display: block;
  font-family: SegoeUI_bold;
  font-weight: bold;
  font-size: 14px;
  line-height: 19px;
  text-align: center;
  color: #000;
  margin-bottom: 0px;
}
